.font {
    font-size: 20px;
    font-weight: bold;
  }
  
  .button{
    cursor: pointer;
  }
  
  .yes{
    color: white;
    font-weight: bold;
    background-color: rgb(40, 240, 40);
    border-radius: 10px;
    padding: 10px;
    cursor: pointer;
  }
  
  .no{
    font-weight: bold;
    background-color: orange;
    border-radius: 10px;
    padding: 10px;
    cursor: pointer;
  }
    
  .inside{
    padding-left: 4px;
    padding-top: 1px;
    font-size: 20px;
  }
  
  .items{
    flex-direction: row;
    display: flex;
  }