.collection-item {
  width: 95%;
  display: flexbox;
  flex-direction: column;
  height: 350px;
  align-items: center;
  position: relative;

  .image {
    width: 100%;
    height: 95%;
    background-size: cover;
    background-position: center;
    margin-bottom: 5px;
  }

  .custom-button {
    width: 81%;
    opacity: 0.7;
    position: absolute;
    top: 255px;
    left: 30px;
    display: none;
  }

  &:hover {
    .image {
      opacity: 0.8;
    }

    .custom-button {
      opacity: 0.85;
      display: flex;
    }
  }

  .collection-footer {
    width: 100%;
    height: 5%;
    display: flex;
    justify-content: space-between;
    font-size: 18px;

    .name {
      text-align: start;
      width: 60%;
      // padding: 0px 5px;
      margin-bottom: 15px;
    }

    .price {
      text-align: end;
      // padding: 0px 5px;
      width: 20%;
    }
  }
}
